.create-trip-container {
  .button {
    display: block;
    margin: 10px 0;
    border-radius: 1px 21px 21px 1px;
  }
  .user-angle-container {
    box-sizing: border-box;
    .field-info {
      color: white;
      position: absolute;
      top: 10px;
      left: 10px;
      font-size: 10px;
    }
    width: 100%;
    background: linear-gradient(45deg, #3498db, #9ec68a);
    text-align: center;
    font-size: 60px;
    color: white;
    padding: 30px 0;
    margin: 10px 5px;
    position: relative;

    input {
      width: 100%;
      height: 36px;
      font-size: 20px;
      padding: 20px 0;
      text-align: center;
    }
    .alt-icon {
      margin-right: 20px;
    }
  }
  .action-btn-container {
    position: fixed;
    bottom: 0;
    text-align: center;
    width: 100%;
    z-index: 100;
    .button {
      margin-right: 10px;
    }
  }
}
